/* GRID CARD */
.event-card {
  background: #fff;
  border-radius: 18px;
  padding: 14px;
}

.card-img-wrapper {
  position: relative;
  overflow: hidden;
  border-radius: 14px;
}

.card-img-wrapper img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  display: block;
}

/* STATUS */
.status-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 20px;
  background: #eee;
}

/* BODY */
.event-body h6 {
  margin: 14px 0 10px;
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* ACTIONS */
.event-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.edit-btn {
  border: 1px solid #7a12ff;
  color: #7a12ff;
  background: transparent;
  border-radius: 20px;
  padding: 4px 14px;
  font-size: 13px;
}

/* MENU */
.menu-wrapper {
  position: relative;
}

.menu-btn {
  border: none;
  background: none;
  font-size: 20px;
}

.menu-dropdown {
  position: absolute;
  right: 0;
  top: 26px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.1);
  z-index: 10;
}

.menu-dropdown button {
  display: block;
  width: 100%;
  padding: 8px 14px;
  border: none;
  background: none;
  text-align: left;
}

/* LIST VIEW */
.list-card {
  display: flex;
  align-items: center;
  gap: 16px;
  background: #fff;
  padding: 14px;
  border-radius: 14px;
  margin-bottom: 12px;
}

.list-img {
  width: 120px;
  height: 70px;
  object-fit: cover;
  border-radius: 10px;
}

.list-info {
  flex: 1;
}
